* {
  color: white;
  margin: 0;
  padding: 0;
  text-decoration: none;
  font-family: tilt neon;
}

.App {
  text-align: center;
}

body {
  background-color: rgb(0, 0, 0);
}

.ppic {
  border-radius: 100px;
  width: 3cm;
  height: 3cm;
}

.profile-box {
  margin-top: 1cm;
  display: flex;
  align-items: center;
  row-gap: .5cm;
  flex-direction: column;
  padding: 0cm 10vw;
}

.Profile {
  margin-bottom: 2cm;
}

.img-div {
  display: flex;
  justify-content: flex-start;
  margin-top: .02cm;
  column-gap: .125cm;
  align-items: center;
}

.cmnt-form-box {
  width: 100%;
  display: flex;
  align-items: center;
  column-gap: .15cm;
  margin-top: .2cm;
}

.cmnt-form {
  width: 100%;
  background-color: transparent;
  color: white;
  border: 2px solid #424242;
  border-radius: 5px;
  padding: .1cm .15cm;

  &:focus {
    outline: none;
    border: #616161 2px solid;
  }
}

.img-div-pp {
  display: flex;
  justify-content: flex-start;
  // margin-top: .02cm;
  column-gap: .21cm;
  align-items: center;
}

.heart {
  width: .42cm;
  height: .42cm;
  cursor: pointer;
}

.heart-pp {
  width: .57cm;
  height: .57cm;
  cursor: pointer;
}

.followers {
  font-size: 15px;
  color: #757575;
  cursor: pointer;
}

.follow-button {
  margin-top: .3cm;
  background-color: transparent;
  border: 2px solid #424242;
  padding: .15cm .5cm;
  width: 100%;
  border-radius: 8px;
  cursor: pointer;
  transition: .25s;

  &:hover {
    background-color: #424242;

  }
}

.profile-heading {
  display: flex;
  align-items: center;
  column-gap: .3cm;
}

.home-box {
  margin-top: 1cm;
  row-gap: .5cm;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.phead {
  color: rgb(113, 113, 113);
  font-size: 18px;
  cursor: pointer;
  transition: .25s;

  &:hover {
    // font-size: 20px;
    color: white;
  }

}

.phead-active {
  color: white;
}

.profile-box-container {
  justify-content: center;
  display: flex;
  column-gap: .3cm;
  align-items: center;
  width: fit-content;
  border: 2px solid #16161c;
  background-color: #0f0f13;
  padding: .6cm .8cm;
  border-radius: 10px;

}

.name-box {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.button-pro {
  display: flex;
  justify-content: center;
  width: 100%;
}

.user-name {
  font-size: 30px;
}

.user-name-para {
  font-size: 20px;
  color: rgb(121, 121, 121);
}

.AuthScreen {
  display: flex;
  justify-content: center;
  align-items: center;

}

.auth-box {
  margin-top: 3cm;
  width: 16cm;
  // border: 1px solid white;
  border-radius: 10px;
  background-color: #0f0f13;
  padding: .3cm .6cm;
}

input:focus {
  outline: none;
  border: 3px solid rgb(81, 81, 81);
}

#noUser {
  display: none;
}

.welcome {
  margin-bottom: -.4cm;
}

#usernameExist {
  display: none;
}

#whitespaceFound {
  display: none;
}

#twoDots {
  display: none;
}

#otherThanUnderscore {
  display: none;
}

#capitalUsername {
  display: none;
}

.error {
  font-size: 15px;
  color: rgb(252, 47, 47);
}

.photo-page-box {
  margin-bottom: 2cm;
}

.comments-head {
  color: #9e9e9e;
}

.success {
  font-size: 15px;
  color: green;
}

.no-comments {
  color: #525252;
  font-size: 14.5px;
}

.tagline {
  font-size: 62px;
  line-height: 1.75cm;
  // font-weight: 600;
  color: rgb(243, 243, 243);

}

.comment {
  font-size: 14.5px;
  color: rgb(190, 190, 190);
}

.comments-box {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.sub-tagline {
  font-size: 20px;
  margin-top: .3cm;
  color: rgb(96, 96, 96);

}

.google-button {
  margin-top: 1.3cm;
  margin-bottom: .5cm;
  background-color: transparent;
  color: white;
  border: 3px solid #424242;
  padding: .3cm .7cm;

  border-radius: 10px;
  font-size: 18px;
  cursor: pointer;
  transition: .25s;

  &:hover {
    background-color: #424242;
  }
}

.Notifications {
  padding: 0cm 10vw;
}

.notifications-box {
  border-radius: 10px;
  margin-top: 1cm;
  padding: .2cm .4cm;
  background-color: #0f0f13;
}

.comment-button {
  cursor: pointer;
  padding: .1cm .3cm;
  border-radius: 5px;
  border: 2px solid #424242;
  background-color: transparent;
  color: white;
  transition: .2s;

  &:hover {
    background-color: #424242;
  }
}

.Nav {
  padding: .3cm 10vw;
  background-color: #0f0f13;
}

.name {
  margin-top: .1cm;
  font-size: 30px;
  cursor: pointer;
}

.setPassword {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  position: fixed;
  z-index: 10;
  display: flex;
  justify-content: center;

  backdrop-filter: blur(10px);


}

.saveU {
  width: 100%;
  background-color: transparent;
  color: white;
  border: 3px solid #424242;
  padding: .3cm .7cm;

  border-radius: 10px;
  font-size: 18px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  transition: .25s;

  &:hover {
    background-color: #424242;

  }

}

#setUsername {
  display: none;
}


.user-form {
  display: flex;
  row-gap: .3cm;
  flex-direction: column;
  align-items: center;
  width: 100%;
  justify-content: center;
}

.user-input {
  background-color: transparent;
  color: white;

  border: 3px solid #424242;
  font-size: 18px;
  padding: 0.3cm 0.3cm;
  /* Adjusted padding values */
  width: calc(100% - 0.8cm);
  /* Adjusted width to account for padding */
  border-radius: 10px;
}

.logo-box {
  display: flex;
  column-gap: .1cm;
  align-items: center;
}

.logo {
  width: 1cm;
  height: 1cm;
}

.log-out-box {

  font-size: 16px;
  margin-top: .3cm;
  cursor: pointer;
  color: rgb(97, 97, 97);
  transition: .2s;

  &:hover {
    color: white;
  }
}

.username-box-head {
  width: 100%;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

.username-box {
  display: flex;
  width: 10cm;
  margin-top: 3cm;
  height: fit-content;
  row-gap: .3cm;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #0f0f13;
  padding: .0cm .5cm .5cm .5cm;
  border-radius: 10px;
}

.search-input {
  width: calc(100% - 0.6cm);
  background-color: transparent;
  border: none;
  padding: .3cm .3cm .2cm .07cm;
  font-size: 18px;
  border-radius: 10px;


  &:focus {
    outline: none;
    border: none;
  }
}

.su {
  transition: .3s;
  cursor: pointer;
  padding: .2cm .4cm .3cm .4cm;

  &:hover {
    border: 2px solid white;
  }
}




.no-user-found-box {
  margin-top: .5cm;
}

.ghost {
  width: 6cm;
  height: 6cm;

}

.notext {
  color: #5f5f5f;
}

.searchedUsersList {
  margin-top: .5cm;
  display: flex;
  justify-content: center;
  column-gap: .4cm;
  row-gap: .4cm;
  flex-wrap: wrap;
}

.search-para {
  color: rgb(98, 98, 98);
}

.Search {
  padding: 0cm 10vw;
}

.noUser-box {
  background-color: #0f0f13;
  width: fit-content;
  padding: .2cm 1cm .4cm 1cm;
  border-radius: 10px;
  margin-top: 3cm;
  height: fit-content;
}

.noUserBox-head {
  display: flex;
  justify-content: flex-end;
  margin-bottom: .25cm;
}

.uh-oh {
  font-size: 30px;
}

.search-input-box {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 0.0cm .25cm .2cm .25cm;
  border-radius: 10px;
  border: 3px solid #424242;
}

.searchImg {
  width: .7cm;
  margin-top: .15cm;
  height: .7cm;
  opacity: .45;
  transform: rotate(20deg);
}

.uh-oh-sub {
  color: rgb(97, 97, 97);
}

.notUser {
  position: fixed;
  z-index: 100;
  top: 0;
  margin-top: 1.5cm;
  bottom: 0;
  left: 0;
  right: 0;
  backdrop-filter: blur(10px);
  display: flex;
  justify-content: center;
}

#logOutConfirm {
  display: none;
}

.lbuttons {
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: .5cm;
}

.logOutUser {
  position: fixed;
  z-index: 100;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  backdrop-filter: blur(10px);
  display: flex;
  justify-content: center;
}

.search-box {
  width: 100%;
  display: flex;
  flex-direction: column;
  row-gap: .2cm;
  margin-top: 1cm;
  align-items: flex-start;
  // border: 1px solid white;
}

.add-photo-form {
  display: flex;
  flex-direction: column;
  row-gap: .3cm;
}

.menu-item {
  cursor: pointer;

  font-size: 16px;
  color: rgb(98, 98, 98);
  transition: .2s;

  &:hover {
    color: white;
  }
}

.add-post-button {
  width: 100%;
  margin: 0;
}

.preview {
  width: 6cm;
  height: auto;
  // margin-top: .2cm;
}

/* CustomFileInput.scss */
.custom-file-input {
  position: relative;

  .custom-file-label {
    background-color: #007BFF;
    color: #fff;
    padding: .3cm .7cm;
    border: 1px solid #007BFF;
    font-size: 18px;
    border-radius: 5px;
    width: calc(100% - 1.4cm);
    cursor: pointer;
    display: inline-block;
  }

  .input-file {
    display: none;
  }

  .custom-file-label:hover {
    background-color: #0056b3;
  }
}


.activeMenu {
  color: white;
}


.photos-box {

  column-count: 4;
  column-gap: .3cm;


}

.photo-page-username {
  font-size: 18px;
  color: rgb(179, 179, 179);
}

.username-sec {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.follow-pp {
  background-color: transparent;
  border: 2px solid #424242;
  color: white;
  padding: .12cm .35cm;
  border-radius: 6px;
  cursor: pointer;
  transition: .2s;

  &:hover {
    background-color: #424242;
  }
}

.photoImg {
  width: 100%;
  height: auto;
  border-radius: 8px;
  cursor: pointer;
}

.photo-page-img {
  width: 100%;
  height: auto;
  border-radius: 10px;
}

.PhotoPage {
  display: flex;
  justify-content: center;
}

.photo-page-box {
  margin-top: 1cm;
  display: flex;
  flex-direction: column;
  row-gap: .2cm;
  border-radius: 10px;
  align-items: flex-start;
  width: 12cm;
  background-color: #0f0f13;
  padding: .2cm .4cm .3cm .4cm;
}

.photo-username {
  text-align: left;
  margin-bottom: .1cm;
  font-size: 15px;
  cursor: pointer;
}

.postphoto {
  text-align: left;
}

.photo {

  width: 6.5cm;
  margin-bottom: .2cm;
  // display: inline-block;
  break-inside: avoid;
}




.photo-username {
  color: rgb(179, 179, 179);
}

.AddPhoto {
  display: flex;
  justify-content: center;
}

.add-photo-box {
  margin-top: 1cm;
  width: 10cm;
  padding: .5cm .6cm;
  border-radius: 10px;
  background-color: #0f0f13;
  height: auto;
}

.menu {
  display: flex;
  column-gap: .5cm;
  align-items: center;
}

.nav-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}


::-webkit-scrollbar {
  width: 0px;
}